'use client'

import { useState } from 'react'

const goals = [
  { id: 1, text: 'Master code', progress: 75 },
  { id: 2, text: 'Build products', progress: 60 },
  { id: 3, text: 'Open source', progress: 40 },
  { id: 4, text: 'Global team', progress: 30 },
]

export default function GoalTracker() {
  const [hovered, setHovered] = useState<number | null>(null)

  return (
    <div className="w-full my-6 border border-foreground/10 p-6 bg-background">
      <div className="text-xs font-mono opacity-60 mb-4">GOALS</div>
      <div className="space-y-4">
        {goals.map((goal) => (
          <div
            key={goal.id}
            className="group cursor-pointer"
            onMouseEnter={() => setHovered(goal.id)}
            onMouseLeave={() => setHovered(null)}
          >
            <div className="flex items-center justify-between mb-2">
              <span className="text-xs font-mono opacity-60 group-hover:opacity-100 transition-opacity duration-200">
                {goal.text}
              </span>
              <span className="text-xs font-mono opacity-40">
                {goal.progress}%
              </span>
            </div>
            <div className="h-1 bg-foreground/10 overflow-hidden">
              <div
                className="h-full bg-foreground/20 transition-all duration-500 ease-out"
                style={{
                  width: hovered === goal.id ? '100%' : `${goal.progress}%`,
                }}
              ></div>
            </div>
          </div>
        ))}
      </div>
    </div>
  )
}

