'use client'

import { useState } from 'react'

const techs = [
  { name: 'Next.js', year: '2022' },
  { name: 'TypeScript', year: '2022' },
  { name: 'MongoDB', year: '2023' },
  { name: 'Cloudinary', year: '2023' },
  { name: 'BYL/QPay', year: '2024' },
]

export default function TechTimeline() {
  const [hovered, setHovered] = useState<number | null>(null)

  return (
    <div className="w-full my-6 border border-foreground/10 p-6 bg-background">
      <div className="text-xs font-mono opacity-60 mb-4">TECH STACK</div>
      <div className="relative">
        <div className="absolute left-0 top-1/2 w-full h-px bg-foreground/10 -translate-y-1/2"></div>
        <div className="flex justify-between items-center relative">
          {techs.map((tech, index) => (
            <div
              key={index}
              className="flex flex-col items-center gap-2 cursor-pointer group"
              onMouseEnter={() => setHovered(index)}
              onMouseLeave={() => setHovered(null)}
            >
              <div
                className={`w-3 h-3 border-2 border-foreground/30 bg-background transition-all duration-300 ${
                  hovered === index ? 'border-foreground/80 scale-150' : ''
                }`}
              ></div>
              <div className="text-xs font-mono opacity-40 group-hover:opacity-100 transition-opacity duration-200 text-center">
                <div>{tech.name}</div>
                <div className="text-[10px] mt-1">{tech.year}</div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  )
}

