'use client'

import { useState } from 'react'

const skills = [
  'Next.js',
  'TypeScript',
  'React',
  'Tailwind',
  'MongoDB',
  'AI/ML',
  'Design',
  'Automation',
]

export default function SkillGrid() {
  const [selected, setSelected] = useState<number[]>([])

  const toggleSkill = (index: number) => {
    setSelected((prev) =>
      prev.includes(index) ? prev.filter((i) => i !== index) : [...prev, index]
    )
  }

  return (
    <div className="w-full my-6 border border-foreground/10 p-6 bg-background">
      <div className="text-xs font-mono opacity-60 mb-4">SKILLS</div>
      <div className="grid grid-cols-4 gap-3">
        {skills.map((skill, index) => (
          <button
            key={index}
            onClick={() => toggleSkill(index)}
            className={`px-3 py-2 border border-foreground/10 text-xs font-mono transition-all duration-200 hover:border-foreground/30 ${
              selected.includes(index)
                ? 'border-foreground/60 bg-foreground/5'
                : 'opacity-60 hover:opacity-100'
            }`}
          >
            {skill}
          </button>
        ))}
      </div>
      {selected.length > 0 && (
        <div className="mt-4 text-xs font-mono opacity-40">
          {selected.length} selected
        </div>
      )}
    </div>
  )
}

